@import '../../../base';

.business-enquiry-container {
  .popup {
    &:before {
      background: linear-gradient(150.14deg, #6480EB 0.6%, #433BC4 100%);
      opacity: 0.9;
    }
  }
}

.business-enquiry-modal {
  max-width: 458px !important;

  .modal-header {
    padding-bottom: 0;
    padding-left: 0;
  }

  .modal-body {
    padding-left: 0;
    padding-right: 0;    
  }

  .modal-footer {
    button {
      margin: auto;
    }
  }

  .checkbox-input-desc {
    color: $color-input-form-landing-desc2;
    font-size: $font-size-tiny;
    font-weight: $font-weight-normal;
    transform: translateY(3px);
  }
}