@import '../../../../../base';

.testimonial-container {
  .testimonial {
    @media screen and (min-width: 992px) {
      max-width: 960px;
    }
    padding: 15px;
    margin: auto;

    .header-container {
      margin: auto;
      @media screen and (min-width: 992px) {
        display: flex;
        justify-content: center;
        transform: translateX(-5%);
      }

      .header {
        p {
          font-weight: $font-weight-bold;
          font-size: $font-size-extra-tiny;
          text-align: center;

          color: $color-input-form-landing-desc;

          @media screen and (min-width: 992px) {
            font-size: $font-size-extra-tiny * 2;
          }
        }

        h4 {
          font-family: $font-after-night;
          font-size: $font-size-extra-big;
          text-align: center;

          color: $color-input-text;
          @media screen and (min-width: 992px) {
            font-size: $font-size-extra-big + $font-size-tiny;
          }
        }
      }
    }

    .testimonial-item-container {
      @media screen and (max-width: 991px) {
        flex: none;
        max-width: none;
        margin-bottom: 30px;
      }

      .testimonial-item {
        margin: auto;
        max-width: 300px;
        min-height: 300px;
        height: 100%;
        padding: 20px 25px;
        background: white;
        box-shadow: 0px 30px 20px rgba(0, 0, 0, 0.03);
        border-radius: 20px;
        display: flex;
        flex-direction: column;

        @media screen and (max-width: 370px) {
          min-height: 520px;
        }

        @media screen and (max-width: 991px) {
          min-height: 350px;
        }
  
        .desc {
          font-weight: $font-weight-semibold;
          font-size: $font-size-smallest;
          color: $color-input-form-landing-desc2;
  
          flex: 1;
        }
  
        .title {
          font-weight: $font-weight-bold;
          font-size: $font-size-regular;
          color: $color-input-text;
        }
  
        .sub-title {
          font-weight: $font-weight-bold;
          font-size: $font-size-smallest;
  
          color: $color-input-form-landing-desc;
        }
      }
    }
    

    .comma-up {
      margin-right: 10px;
    }

    .comma-down {
      transform: translateY(-35px) translateX(-30px) rotate(180deg);
      @media screen and (max-width: 991px) {
        transform: translateY(-25px) translateX(-30px) rotate(180deg);
      }
    }

    .comma-up,
    .comma-down {
      @media screen and (max-width: 991px) {
        img {
          height: auto;
          width: 50px;
        }
      }
    }
  }
}