@import '../../../../base';
@import '../BusinessLanding.scss';

.business-main-container {
  .why-container {
    margin-bottom: 50px !important;
    @media screen and (max-width: 991px) {
      margin-top: 100px !important;
      margin-bottom: 10px !important;
    }
  
    .main-title {
      color: #F2F4FD;
    }

    .why-desc {
      max-width: 635px;
      margin: 20px auto 50px auto;
      text-align: center;
      font-size: $font-size-smallest;
      font-weight: $font-weight-semibold;
    }

    .why-item {
      @media screen and (max-width: 991px) {
        flex: none;
        max-width: none;
      }

      .why-info {
        margin: auto;
        max-width: 200px;
        display: flex;
        flex-direction: column;
        align-items: center;
  
        img {
          max-width: 160px;
          max-height: 160px;
        }
  
        h5 {
          margin: auto;
          font-weight: $font-weight-bold;
          font-size: $font-size-regular;
          text-align: center;
          color: $color-input-text;
        }
  
        p {
          margin-top: 12px;
          font-weight: $font-weight-semibold;
          font-size: $font-size-smallest;
          text-align: center;
          color: $color-input-form-landing-desc2;
        }
      }
    }

    @media screen and (max-width: 1023px) {
      margin-top: 40px;
      margin-bottom: 20px;
    }
  }
}
