@import '../../../../base';
@import '../BusinessLanding.scss';

%standard-font {
  font-family: $font-open-sans;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  text-align: center;
  color: white;
}

.business-main-container {
  .highlight-outer-container {
    background-color: $product-background;
    margin-bottom: 60px;

    .highlight-container {
      margin-bottom: 80px;

      @media screen and (max-width: 991px) {
        margin-bottom: 80px;
      }
      @media screen and (max-width: 767px) {
        margin-bottom: 10px;
      }

      .slick-slider {
        transform: none;
      }

      .highlight-items {
        max-width: 100%;
        width: 900px;
        margin: auto;
      }

      .highlight-desc {
        max-width: 635px;
        margin: 20px auto 50px auto;
        text-align: center;
        font-size: $font-size-smallest;
        font-weight: $font-weight-semibold;
      }

      .margin-padding {
        padding-left: 0;
        padding-right: 0;
      }

      .quote-main {
        text-align: center;
      }

      .highlight-info {
        margin: auto;
        max-width: 200px;
        display: flex;
        flex-direction: column;
        align-items: center;

        img {
          max-width: 160px;
          max-height: 160px;
        }

        h5 {
          margin: auto;
          font-weight: $font-weight-bold;
          font-size: $font-size-regular;
          text-align: center;
          color: $color-input-text;
        }

        p {
          margin-top: 12px;
          font-weight: $font-weight-semibold;
          font-size: $font-size-smallest;
          text-align: center;
          color: $color-input-form-landing-desc2;
        }
      }
    }
    .details-container {
      background-color: $color-input-form-landing-desc;
      color: #ffffff;

      .main-title {
        margin-top: 20px;
        opacity: 0.5;
      }
      .sub-title {
        color: #FFFFFF !important;

        @media screen and (max-width: 1023px) {
          margin-top: 20px;
        }
      }
      h5 {
        color: white;
        font-weight: $font-weight-semibold;
        font-size: $font-size-smallest;
      }

      .highlight-img {
        text-align: right;
        img {
          width: 100%;
          height: auto;
          max-width: 300px;
        }
      }
      .quote {
         margin-top: 10px;
      }

      @media screen and (max-width: 767px) {
        text-align: center !important;
        padding: 20px 0px 30%;

        .highlight-img {
          text-align: center;
        }
        .quote {
           margin-top: 20px;
        }
      }
    }

    .inverted-comma-top {
      position: relative;
      left: 0px;
      transform: scale(0.8);
      opacity: 0.8;
      color: $color-hover;
      width: 74px;
      height: 71px;
      top: -8%;

      @media screen and (min-width: 768px) {
        margin-right: 3%;
        margin-left: -4%;
        bottom: -8px;
      }
      @media screen and (min-width: 1440px) {
        bottom: -20px;
      }
    }

    .cursive-title {
      @extend %standard-font;
      font-family: $font-after-night;
      font-size: $font-size-extra-big;
      color: $color-input-text;
      margin-top: 8px;
      margin-bottom: 70px;

      .green {
        color: $color-hover;
      }

      @media screen and (max-width: 991px) {
        margin-bottom: 20px;
      }
    }

    .inverted-comma-bottom {
      position: relative;
      left: 0px;
      opacity: 0.8;
      width: 124px;
      height: 111px;
      margin-top: -30px;
      transform: rotate(180deg) scale(0.4);
      z-index: 99;

      @media screen and (min-width: 768px) {
        left: 70%;
      }
      @media screen and (min-width: 1440px) {
        width: 154px;
        height: 141px;
        left: 75%;
        margin-top: -60px;
      }
    }

    .grandpa-img {
      position: absolute;
      bottom: -60px;
      right: 20%;

      img {
        max-height: 200px;
      }

      @media screen and (min-width: 2560px) {
        right: 30%;
      }
    }

    @media screen and (max-width: 1023px) {
      padding-top: 40px !important;
    }
  }
}
