@import '../../../../base';
@import '../BusinessLanding.scss';

.business-main-container {
  .get-coverage-container {
    position: relative;
    margin-top: -70px !important;
    padding: 0px 100px 50px !important;
    transform: translateY(185px);
    z-index: 99;
    background: white;
    box-shadow: 0px 40px 30px rgba(0, 0, 0, 0.2);
    border-radius: 20px;
    max-width: 1016px !important;
    display: flex;
    flex-direction: row;
    align-items: center;

    .sub-title {
      margin-bottom: 20px;
      @media screen and (max-width: 991px) {
        text-align: center;
      }
    }

    img {
      width: 100%;
      height: auto;
      max-width: 450px;
    }

    h5 {
      font-size: $font-size-smallest;
      font-weight: $font-weight-semibold;
    }

    .right-container {
      img {
        max-width: 100%;
        
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }

    @media screen and (max-width: 991px) {
      padding: 0px 15px 40px 15px !important;

      img {
        margin-top: 20px;
      }
      .left-container {
        padding-left: 0px;
        padding-right: 0px;
      }
      .input-wrapper-main-v3 {
        padding: 0px;
      }
      .footnote {
        // margin-top: 40px;
        text-align: center;
      }

      .right-container {
        img {
          max-height: 100px;
        }
      }
    }
    // @media screen and (max-width: 1020px) {
    //   padding: 0px 55px 50px;
    //   flex-direction: column;
    //   text-align: center;
    // }
  }

  .learn-more-button {
    margin-top: 10px;
    margin-bottom: 15px;
    @media screen and (max-width: 991px) {
      display: flex;
      justify-content: center;
    }
  }
}
