@import '../../../../base';
@import '../BusinessLanding.scss';

.business-main-container {
  .benefits-container {
    background-color: $product-background;
    
    .benefits-inner-container {
      .main-title {
        color: #F2F4FD;
      }
      .benefits-content {
        text-align: center;
        @media screen and (min-width: 992px) {
          padding: 20px 40px;
        }
      }
      .highlight-container {
        justify-content: center;
        align-items: flex-end;
        @media screen and (max-width: 991px) {
          margin-bottom: 30px;
        }
      }
      .highlight-text {
        color: $color-input-form-landing-desc;
        font-size: $font-size-small;
        font-weight: $font-weight-bold;
        width: 50%;
        max-width: 180px;
      }
      h6 {
        font-size: $font-size-smallest;
        font-weight: $font-weight-semibold;
      }
      .description {
        max-width: 600px;
        margin: auto;
      }
      .benefits-img {
        text-align: center;

        img {
          width: 100%;
          max-width: 350px;
        }
      }
      .promo-button {
        margin: 30px auto;
      }

      @media screen and (min-width: 991px) {
        .highlight-container {
          justify-content: flex-start;
        }
        .benefits-content div {
          text-align: left;
        }

        .benefits-img {
          text-align: right !important;
        }
      }

      @media screen and (max-width: 1023px) {
        margin-top: 40px !important;
        margin-bottom: 20px !important;
      }
    }
  }
}
