@import '../../../../base';
@import '../BusinessLanding.scss';

.business-main-container {
  .questions-container {
    max-width: 950px !important;
    margin-top: 100px !important;
    padding: 0 55px !important;
    align-items: center;

    @media screen and (max-width: 575px) {
      align-items: flex-start;
    }


    p {
      @extend  %description;
    }
    .business-img-container {
      display: flex;
      justify-content: flex-end;

      @media screen and (max-width: 991px) {
        display: block;
      }
      .business-img {
        margin-right: 50px;
        max-width: 263.5px;
        max-height: 300px;
        width: 100%;
        height: auto;
        // margin: auto;

        @media screen and (max-width: 400px) {
          max-width: 92px;
          max-height: 104px;
        }
      }
    }
    
    .contents {
      padding: 0 40px;

      @media screen and (max-width: 575px) {
        h2 {
          font-size: $font-size-regular;
        }

        p {
          font-size: $font-size-tiny;
        }
      }
    }

    .whatsapp-container {
      position: relative;
      margin-top: 30px;
      max-width: 330px;
      width: 100%;

      @media screen and (max-width: 575px) {
        margin-top: 0;
      }

      img {
        position: absolute;
        width: 25%;
        height: auto;
        right: -1%;
        top: 25%;
      }

      .whatsapp-inner {
        width: 80%;
        position: relative;
        @media screen and (max-width: 575px) {
          width: 100%;
        }
      }

      .whatsapp {
        background: white;
        /* Mild shadow */
        box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05);
        border-radius: 20px;
        text-align: center;
        padding: 15px 15px 5px;

        a {
          font-weight: $font-weight-bold;
          font-size: $font-size-extra-tiny * 3;
          color: $color-input-form-landing-desc2;

          @media screen and (max-width: 575px) {
            font-size: $font-size-small;
          }
        }
      }

      h4 {
        padding-top: 10px;
        font-weight: bold;
        font-size: $font-size-extra-tiny;
        color: $color-hover;
        margin-bottom: 0px;
        text-align: left;
      }

      .big-whatsapp {
        @media screen and (max-width: 500px) {
          .whatsapp-inner {
            width: 100%;
          }
        }

        img {
          width: 157px;
          height: 157px;
          transform: translateX(-15px) translateY(-45px);

          @media screen and (max-width: 500px) {
            display: none;
          }
        }
      }

      .small-whatsapp {
        @media screen and (max-width: 991px) {
          margin-top: 15px;
        }

        @media screen and (max-width: 420px) {
          .whatsapp-inner {
            width: 100%;
          }
        }

        img {
          width: 94px;
          height: 94px;
          transform: translateY(9px);

          @media screen and (max-width: 420px) {
            display: none;
          }
        }
      }
    }

    .whatsapp-non-resp {
      display: none;
      @media screen and (min-width: 428px) {
        display: block;
      }
    }

    .whatsapp-resp {
      display: block;
      @media screen and (min-width: 428px) {
        display: none;
      }
    }

    @media screen and (max-width: 767px) {
      margin-left: auto;
      margin-right: auto;
      text-align: center;
      padding: 0 15px !important;

      .contents {
        padding: 0 0 0 10px;
      }

      .whatsapp-container,
      .whatsapp-inner {
        margin-left: auto;
        margin-right: auto;
      }
      .whatsapp-container > img {
        display: none;
      }
      .business-img {
        margin-bottom: 20px;
      }
    }
    @media screen and (min-width: 768px) {
      display: flex;
    }
  }
}
