@import '../../../../base';
@import '../BusinessLanding.scss';

.business-main-container {
  .dashboard-container {
    margin-top: 60px !important;
    margin-bottom: 40px !important;

    .header-container {
      @media screen and (min-width: 992px) {
        margin-top: 20px !important;
        margin-bottom: 50px;
      }
    }

    .main-title {
      color: #F2F4FD;
    }

    .profile-img {
      width: 100%;
      padding-right: 56px;

      img {
        width: 100%;
        height: auto;
      }
    }

    .dashboard-header {
      margin-bottom: 50px;

      h4 {
        font-weight: $font-weight-bold;
        font-size: $font-size-regular;
        color: $color-input-text;
      }

      p {
        @extend  %description;
      }
    }

    .dashboard-info-container {
      align-items: center;
      justify-content: center;
      display: flex;
      flex-direction: column;
      margin-top: 20px;

      @media screen and (max-width: 991px) {
        max-width: 600px;
        margin-left: auto;
        margin-right: auto;
      }
    }

    .dashboard-info {
      display: flex;
      align-items: flex-start;
      margin-bottom: 30px;

      @media screen and (min-width: 768px) and (max-width: 1023px) {
        margin-left: auto;
        margin-right: auto;
        max-width: 350px;
      }

      img {
        margin-right: 10px;
      }

      h5 {
        font-weight: $font-weight-bold;
        font-size: $font-size-smallest;
        color: $color-input-text;
        margin: 0;
      }

      p {
        @extend  %description;
        margin-top: 5px;
      }
    }

    @media screen and (max-width: 1023px) {
      margin-top: 50px !important;
      margin-bottom: 20px !important;

      .profile-img {
        text-align: center;
        padding-top: 20px;
        padding-right: 0px;
      }
      .dashboard-header {
        text-align: center;
        margin-top: 20px;
      }
    }

    @media screen and (min-width: 768px) and (max-width: 991px) {
      margin-top: 100px !important;
    }
  }
}
