@import '../../../../base';
@import '../BusinessLanding.scss';

.business-main-container {
  .coverage-container {
    @media screen and (max-width: 991px) {
      margin-top: 100px;
    }

    .main-title {
      margin-top: 20px;
      color: #F2F4FD;
    }
    .coverage-desc {
      max-width: 635px;
      margin: 20px auto 50px auto;
      text-align: center;
      font-size: $font-size-smallest;
      font-weight: $font-weight-semibold;
    }

    .coverage-img {
      text-align: right;
      padding-right: 10px;
      display: flex;
      align-items: center;
      justify-content: center;

      img {
        width: 80%;
        @media screen and (max-width: 767px) {
          max-width: 300px;
          margin: auto;
        }
      }
    }

    .highlight {
      text-align: center;
      max-width: 450px;
      margin: auto;
      font-weight: $font-weight-semibold;
      color: $color-input-form-landing-desc2;
      margin-bottom: 50px;

      h2 {
        margin-top: 20px;
        text-align: center;
      }
      p {
        text-align: center;
      }
      
      @media screen and (min-width: 768px) {
        font-size: $font-size-smallest;
        margin-bottom: auto;
        transform: translateX(-50px) translateY(-20px);
        margin: auto 0;

        h2 {
          text-align: left;
          margin-top: 20px;
        }
        p {
          text-align: left;
        }
      }
    }

    .add-on {
      @media screen and (max-width: 991px) {
        margin-top: 50px;
      }
      color: $color-input-form-landing-desc;
      font-size: $font-size-big;
      font-weight: $font-weight-bold;
      text-align: center;
      padding: 0 0 20px;

      @media screen and (min-width: 992px) {
        padding: 40px 20px;
      }
    }

    .coverage-info {
      max-width: 230px;
      margin: 20px auto auto;
      text-align: center;

      @media screen and (max-width: 1023px) {
        display: block;
        flex-direction: column;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
      }

      img {
        max-width: 150px;
        max-height: 150px;
        margin: auto;
      }

      h5 {
        font-weight: $font-weight-bold;
        font-size: $font-size-regular;
        color: $color-input-text;
      }

      p {
        margin-top: 12px;
        font-weight: $font-weight-semibold;
        font-size: $font-size-smallest;
        color: $color-input-form-landing-desc2;
      }
    }
    @media screen and (max-width: 991px) {
      margin-top: 100px !important;
      margin-bottom: 10px !important;
    }
  }
}
