@import '../../../../base';
@import '../BusinessLanding.scss';

.business-main-container {
  .testimonial-container {
    margin-top: 80px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @media screen and (max-width: 991px) {
      margin-top: 50px;
    }

    .channel-list-container {
      max-width: 1110px;
      padding: 0 40px;
      margin: auto;
      margin-top: 50px;

      @media screen and (max-width: 991px) {
        margin-top: 0;
        margin-bottom: 50px;
      }
  
      .channel-list {
        align-items: center;
        justify-content: center;
        display: flex;
      }

      .channels {
        margin-top: 80px;
        padding-left: 0px;
        max-width: 300px;
        .sponsors {
          height: 100%;
        }

        .nav-link {
          max-height: 71px;
        }
        a {
          height: 100%;
          display: flex;
        }
        img {
          display: flex;
          max-height: 71px;
        }
      }

      .nav-item {
        width: 16.6%;
        max-height: 71px;

        a {
          img {
            object-fit: contain;
            opacity: 1;
            max-width: 100%;
          }
        }

        @media screen and (max-width: 767px) {
          width: 33.3%;
        }
      }

      .channel-img {
        max-width: 150px;
        margin: 10px auto;
        align-items: center;
        justify-content: center;
        display: flex;
        
        img {
          width: 80%;
        }
      }

      .shoutout-img {
        padding-left: 30px;
        position: absolute;
        left: 0;
        max-width: 200px;
        top: -30px;
        left: -40px;

        @media screen and (min-width: 575px) {
          padding-left: 0;
          top: -50px;
          left: -100px;
        }
      }


      @media screen and (min-width: 768px) {
        .channels {
          padding-left: 150px;
          max-width: none;
        }
        .shoutout-img {
          position: absolute;
          top: -8%;
          left: 40px;
          margin: auto;
          transform: translateY(-30px);
          max-width: none;
        }
      }
      @media screen and (min-width: 1024px) {
        .shoutout-img {
          top: -10%;
        }
      }
      @media screen and (min-width: 1440px) {
        .shoutout-img {
          left: 60px;
        }
      }
    }
  }
}
